<template>
  <v-ons-page>
    <div class="content_area">
      <v-ons-list>
        <v-ons-list-header>
          빌린 물건
        </v-ons-list-header>
        <item-row v-for="item in rentItemList" :Item="item" v-bind:key="item.idx"></item-row>
      </v-ons-list>
    </div>

    <v-ons-modal :visible="loading">
      <p style="text-align: center">
        Loading <v-ons-icon icon="fa-spinner" spin></v-ons-icon>
      </p>
    </v-ons-modal>
  </v-ons-page>
</template>

<script>

import ItemRow from './ItemRow.vue';

export default {
  key: "MyPage",
  name: "MyPage",
  toolBarName: "BeBlet",
  data: function() {
    return {
      loading: false,
      get id() {
        return window.localStorage.getItem("id");
      }
    }
  },
  components: {
    ItemRow
  },
  computed: {
    rentItemList() {
      return this.$store.state.rentItemList;
    },
    todayDate: function () {
      let todayObj = new Date();
      let year = todayObj.getFullYear();
      let month = todayObj.getMonth() + 1;
      let date = todayObj.getDate();
      let dateStr = year + "-";
      if (month < 10) {
        month = "0" + month;
      }
      if (date < 10) {
        date = "0" + date;
      }
      dateStr += month;
      dateStr += "-";
      dateStr += date;


      return dateStr;
    },
    currentTime: function () {
      let todayObj = new Date();
      let hours = todayObj.getHours(); // 시

      return hours;
    },
    startDate: function() {
      let startDateObj = new Date(this.Item.start_TIME);
      return JSON.stringify(startDateObj);
    }
  }
}
</script>

<style>

</style>


